
.root-container{
    width:100%;
    height:95vh;
    display:flex;
    margin-top: 10px;
}

.left-container{
    
    flex:1;
    justify-content: flex-start;
    display: flex;
    align-items: flex-start;
    padding: 20px;
      
    width: 70%;
    
        background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);

}
.right-container{
    flex:1;
    flex-flow:column nowrap;
    padding: 20px;
    border-left: 1px gray solid;
    border: 2px solid black;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.5);
    
}

.right-container h2{
    font-family: fantasy;
    color: indianred;
}

.card-container{
    width:200px;
    height:260px;
    background-color: white;
    color: black !important;
    font-family: fantasy;
    font-size: larger;
    display: flex;
    flex-flow:column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.5);
}
.image-style{
    width:80px;
    height: 80px;
    border-radius:50%;
    
}

/* .button-style{
    border-radius:20%;
    background-color:black;
    color: white;
} */

.button-style{
    color: #494949 !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    padding: 9px;
    border: 2px solid #494949 !important;
    display: inline-block;
    transition: all 0.4s ease 0s;
}

.button-style:hover{
    color: #ffffff !important;
    background: #f6b93b;
    border-color: #f6b93b !important;
    transition: all 0.4s ease 0s;
}

.list-container{
    width: 95%;
    height: 60px;
    padding-left:  20px;
    border: 1px solid #494949 !important;
    font-family: fantasy ;
    font-size: large;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border:1px black solid;
}

.quan-style{
    margin:5px;
}

.add-container{
    width: 'auto';
    height: 'auto';
    padding:15px;
    display: flex;
    justify-content: space-around;
}
@media screen and (max-width:700px){
    
    .root-container{
        display: grid;
        grid-template-rows: 1fr 1fr;
       
    }
   
  .left-container{
      flex-wrap: wrap;
    display: grid;
    grid-template-columns: repeat(auto-fit,300px);
    justify-content: center;
    width: 100%;
    grid-gap: 10px;
    height: fit-content;
  }
  .right-container{
      width: 100%;
  }
  .card-container{
      padding: 10px;
  }
}